.form_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

input[type="text"],
input[type="number"] {
  margin-bottom: 10px;
  padding: 5px;
}

button[type="submit"] {
  padding: 7px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

.Car {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
}

.Car img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 10px;
}
