.container{
    padding-top: 60px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.header{
    width: 100%;
    text-align: center;
}

.header h1{
    font-size: 4em;
    text-transform: uppercase;
}

.products{
    display: inline-block;
    justify-content: center;
    flex-wrap: wrap;
    margin: 50px;
}

.product{
    width: 350px;
    height: 100%;
    box-shadow: 0 5px 20px rgba(0, 0, 0, .3);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 20px 40px;
    border-radius: 10px;
    transition: .3s;
}
.product:hover{
    transform: translateY(-15px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
    cursor: pointer;
}
.image{
    display: grid;
    place-content: center;
    width: 100%;
}
.image img{
    width: 250px;
    height: 190px;
}

.name{
    padding: 10px;
}

.name h3{
    font-size: 1.5em;
    font-weight: bold;
    text-transform: uppercase;
}

.name span{
    font-size: 1.4em;
}

.button{
    margin: 10px;
}

button{
    padding: 10px 20px;
    background-color: #001225;
    color: #fff;
    border: none;
    cursor: pointer;
  }

  button:hover {
    background-color: #0056b3;
  }