.container_main{
    background: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)), url("../images/main_photo.jpg") no-repeat center;
    background-size: cover;
    height: 100vh;
    background-attachment: fixed;
}

.container_h{
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

h1{
    font-size: 60px;
}

h2{
    font-size: 40px;
}
