.container_contact{
  background-color: #171515;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container_contact_width{
  display: flex;
  justify-content: center;
  align-items: center;
}

.social_media {
  display: flex;
}

.social_media li a {
  width: 70px;
  height: 70px;
  background-color: #fff;
  text-align: center;
  line-height: 70px;
  font-size: 26px;
  margin: 0 5px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
}

.social_media li a .icon {
  position: relative;
  color: #262626;
  transition: .5s;
  z-index: 3;
}

.social_media li a:hover .icon {
  color: #fff;
  transform: rotateY(360deg);
}

.social_media li a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: .4s;
  z-index: 2;
}

.social_media li a:hover::before{
  top: 0;
}

.social_media li:nth-child(1) a:before{
  background-color: #3b5998;
}

.social_media li:nth-child(2) a:before{
  background-color: #171515;
}

.social_media li:nth-child(3) a:before{
  background-color: rgb(228, 64, 95);
}


.footer {
    display: flex;
    text-align: center;
    justify-content: center;
    background-color: black;
    color: #fff;
    padding: 5px;
    font-weight: bold;
}