.navbar {
    width: 100%;
    height: 80px;
    display: flex;
    position: fixed;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    color: black;
    background-color: #fff;
    text-transform: uppercase;
    z-index: 100;
}


.navbar-menu {
    display: flex;
}

.navbar-item {
    padding: 1rem;
    cursor: pointer;
}

.hamburger{
    display: none;
}


@media screen and (max-width: 940px) {
    .navbar-menu{
        position: fixed;
        left: -100%;
        top: 80px;
        flex-direction: column;
        justify-content: center;
        background-color: rgb(209, 209, 209);
        color: black;
        width: 100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
        z-index: 100;
    }

    .navbar-menu.active{
        left: 0;
    }

    .navbar-item{
        margin: 1.5rem;
        font-size: 1.8rem;
        justify-content: center;
    }

    .hamburger {
        display: flex;
        justify-content: center;
    }
}