@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: var(--white);
}

body {
  color: #333;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.5;
}
